<template>
  <div id="app">
    <div class="column is-half is-offset-one-quarter">
      <h4 class="is-size-4">Pokedex</h4>
      <hr>
      <input class="input is-normal" type="text" placeholder="Buscar pokemon pelo nome" v-model="busca">
      <button class="button is-primary is-fullwidth" @click="buscar()">Buscar</button>
      <div v-for="(poke, index) in filteredPokemons" :key="poke.name">
        <Pokemon :name="poke.name" :url="poke.url" :num="index + 1" />
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import Pokemon from './components/PokemonAPI.vue'
export default {
  name: 'App',
  data() {
    return {
      pokemons: [],
      filteredPokemons: [],
      busca: ''
    }
  },
  created: function () {
    axios.get('https://pokeapi.co/api/v2/pokemon?limit=151&offset=0').then(res => {
      this.pokemons = res.data.results;
      this.filteredPokemons = res.data.results;
    })
  },
  components: {
    Pokemon
  },
  methods: {
    buscar(){
      this.filteredPokemons = this.pokemons;  
      if(!this.busca || this.busca === ' ') {
        this.filteredPokemons = this.pokemons;
      } else {
       this.filteredPokemons = this.pokemons.filter(pokemon => pokemon.name === this.busca.toLowerCase())
      }
    }
  },
  computed: {
    /*
    buscar: function(){
      if(!this.busca || this.busca === '') {
        return this.pokemons;
      } else {
        return this.pokemons.filter(pokemon => pokemon.name === this.busca.toLowerCase())
      }
    } */
  }
}

</script>

